.navMenuOuter{
  position: fixed;
  display: none; // hiden 
  width: 100%;
  height: 100%;
  z-index: 3;
  // background: red; // it is for testing
  // overflow: hidden; // it can hide elements of <.conteiner>
}

.menuButton{
  position: absolute;
  z-index: 2;
  bottom: 2.5rem;
  left: 2.5rem;
}


@media (min-width : $media_ipad_one){
  .navMenuOuter{
    height: 30vh;
    width: 100%;
    bottom: 0;
    // background-color: red;
  }
}

@media (min-width : $media_descTop_one){
  .navMenuOuter{
    height: 100%;
    width: 20rem;
    bottom: 0;
    top: 0;
    left: 0;
  }
  .menuButton{
    bottom: auto;
    top: 1rem;
    // left: px;
  }
}